@import "../../slyles/variables";

.loader {
  display: flex;
  justify-content: center;
  padding: 10px 0;

  grid-column-start: 1;
  grid-column-end: span 3;

  &_icon {
    background-image: url("./icons/loader.svg");
    background-size: contain;
    width: 48px;
    height: 48px;

    &--small {
      width: 35px;
      height: 35px;
    }

    animation: spin 1500ms linear infinite;
  }
}

@keyframes spin {
  100% {
    transform:rotate(360deg);
  }
}
